import React from 'react';
import ReactDOM from 'react-dom';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TableFooter from '@material-ui/core/TableFooter';
import TablePagination from '@material-ui/core/TablePagination';
import Paper from '@material-ui/core/Paper';
import IconButton from '@material-ui/core/IconButton';
import FirstPageIcon from '@material-ui/icons/FirstPage';
import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft';
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight';
import LastPageIcon from '@material-ui/icons/LastPage';

import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';

import MenuItem from '@material-ui/core/MenuItem';
import deburr from 'lodash/deburr';
import superagent from 'superagent';
import constantes from './constantes';
import moment from 'moment';
import html2canvas from 'html2canvas';
import jsPDF from 'jspdf';
import numeral from 'numeral';

let id = 0;
function createData(name, calories, fat, carbs, protein) {
  id += 1;
  return { id, name, calories, fat, carbs, protein };
}

const nombresServicios = [
	'Balata delanteras',
	'Balata traseras',
	'Disco nuevo',
	'Tambor nuevo',
	'Bomba de frenos',
	'Cilindro de rueda',
	'Líquido',
	'Herrajes',
	'Rectificado disco',
	'Rectificado tambor',
	'Bujías',
	'Aceite',
	'Filtro gasolina',
	'Filtro aceite',
	'Filtro aire',
	'Presurisado',
	'CARBUCLINK',
	'ANTICONGELANTE',
	'Amortiguadores delanteros',
	'Amortiguadores traseros',
	'Base amortiguadores delanteros',
	'Base amortiguadores traseros',
	'Bieletas',
	'Terminales',
	'Rótula inferior',
	'Rótula superior',
	'Tornillo estabilizador delantero',
	'Tornillo estabilizador trasero',
	'Bujes superior',
	'Horquilla inferior',
	'Horquilla superior',
	'Goma barra estabilizadora',
	'Goma cremallera',
	'Buje inferior grande',
	'Buje inferior chico',
	'Maza',
	'Balero doble',
];

const rows = [
  createData('Frozen yoghurt', 159, 6.0, 24, 4.0),
  createData('Ice cream sandwich', 237, 9.0, 37, 4.3),
  createData('Eclair', 262, 16.0, 24, 6.0),
  createData('Cupcake', 305, 3.7, 67, 4.3),
  createData('Gingerbread', 356, 16.0, 49, 3.9),
];

const actionsStyles = theme => ({
  root: {
    flexShrink: 0,
    color: theme.palette.text.secondary,
    marginLeft: theme.spacing.unit * 2.5,
  },
});

class TablePaginationActions extends React.Component {
  handleFirstPageButtonClick = event => {
    this.props.onChangePage(event, 0);
  };

  handleBackButtonClick = event => {
    this.props.onChangePage(event, this.props.page - 1);
  };

  handleNextButtonClick = event => {
    this.props.onChangePage(event, this.props.page + 1);
  };

  handleLastPageButtonClick = event => {
    this.props.onChangePage(
      event,
      Math.max(0, Math.ceil(this.props.count / this.props.rowsPerPage) - 1),
    );
  };

  render() {
    const { classes, count, page, rowsPerPage, theme } = this.props;

    return (
      <div className={classes.root}>
        <IconButton
          onClick={this.handleFirstPageButtonClick}
          disabled={page === 0}
          aria-label="First Page"
        >
          {theme.direction === 'rtl' ? <LastPageIcon /> : <FirstPageIcon />}
        </IconButton>
        <IconButton
          onClick={this.handleBackButtonClick}
          disabled={page === 0}
          aria-label="Previous Page"
        >
          {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
        </IconButton>
        <IconButton
          onClick={this.handleNextButtonClick}
          disabled={page >= Math.ceil(count / rowsPerPage) - 1}
          aria-label="Next Page"
        >
          {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
        </IconButton>
        <IconButton
          onClick={this.handleLastPageButtonClick}
          disabled={page >= Math.ceil(count / rowsPerPage) - 1}
          aria-label="Last Page"
        >
          {theme.direction === 'rtl' ? <FirstPageIcon /> : <LastPageIcon />}
        </IconButton>
      </div>
    );
  }
}

TablePaginationActions.propTypes = {
  classes: PropTypes.object.isRequired,
  count: PropTypes.number.isRequired,
  onChangePage: PropTypes.func.isRequired,
  page: PropTypes.number.isRequired,
  rowsPerPage: PropTypes.number.isRequired,
  theme: PropTypes.object.isRequired,
};

const TablePaginationActionsWrapped = withStyles(actionsStyles, { withTheme: true })(
  TablePaginationActions,
);

class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      page: 0,
      rowsPerPage: 20,
      pedidos: [],
      totalPedidos: 0,
      filtroSeleccionado: 'cliente',
      filtroSeleccionadoFinal: 'cliente',
      filtroBusqueda: '',
      filtroBusquedaFinal: '',
      filtrosActivados: false,
      selectedPedido: null,
      selectedPedidoServicios: [],
    };
  }

  componentDidMount() {
    this.getInitialPedidos();
  }

  getInitialPedidos = () => {
    const { BASE_URL } = constantes;
    superagent
      .get(`${BASE_URL}/pedidos`)
      .then((pedidos) => {
        this.setState({
          pedidos: pedidos.body.pedidos,
          totalPedidos: pedidos.body.count,
          filtrosActivados: false,
        });
      })
      .catch((err) => {
        console.error(err);
      });
  }

  getPedidos = (skip) => {
    const {
      filtrosActivados,
      filtroSeleccionadoFinal,
      filtroBusquedaFinal,
    } = this.state;
    const { BASE_URL } = constantes;
    if (filtrosActivados) {
      superagent
        .get(`${BASE_URL}/pedidos`)
        .query({ skip })
        .query({ filter: filtroSeleccionadoFinal })
        .query({ value: filtroBusquedaFinal })
        .then((pedidos) => {
          this.setState({
            pedidos: pedidos.body.pedidos,
            totalPedidos: pedidos.body.count,
          });
        })
        .catch((err) => {
          console.error(err);
        });
    } else {
      superagent
        .get(`${BASE_URL}/pedidos`)
        .query({ skip })
        .then((pedidos) => {
          this.setState({
            pedidos: pedidos.body.pedidos,
            totalPedidos: pedidos.body.count,
          });
        })
        .catch((err) => {
          console.error(err);
        });
    }
  }

  handleChangePage = (event, page) => {
   this.setState({ page, pedidos: [] });
   this.getPedidos(page * 5);
  };

  handleChangeRowsPerPage = event => {
   this.setState({ page: 0, rowsPerPage: event.target.value });
  };

  handleChange = name => (event, { newValue }) => {
    this.setState({
      [name]: newValue,
    });
  };

  print = () => {
    const { selectedPedido } = this.state;
    const filename = `pedido-${selectedPedido.folio}.pdf`;

		html2canvas(document.querySelector('#pdf-to-download'), {
      allowTaint: true,
    })
    .then(canvas => {
			let pdf = new jsPDF('p', 'mm', 'a4');
      const width = pdf.internal.pageSize.getWidth();
      const height = pdf.internal.pageSize.getHeight();
      const divHeight = document.getElementById('pdf-to-download').offsetHeight;
      const divWidth = document.getElementById('pdf-to-download').offsetWidth;
      pdf.addImage(canvas.toDataURL('image/png'), 'PNG', 6, 6, divWidth * 0.22, divHeight * 0.22);
			pdf.save(filename);
		});
	}

  getServiciosByPedido = (row) => {
    const { BASE_URL } = constantes;
    superagent
      .get(`${BASE_URL}/servicios-pedido/${row.id}`)
      .then((servicios) => {
        let totalManoObra = 0;
        let totalPrecio = 0;
        servicios.body.forEach((ser) => {
          totalManoObra += this.getNumberValue(ser.manoObra);
          totalPrecio += (this.getNumberValue(ser.precio) * this.getNumberValue(ser.cantidad));
        })
        this.setState({ selectedPedido: row, selectedPedidoServicios: servicios.body, totalManoObra, totalPrecio });
        try {
          $("html, body").animate({
            scrollTop: $("#print").offset().top
          }, 1000);
        } catch(err) {
          console.error(err);
        }
      })
      .catch((err) => {
        console.error(err);
      });
  }

  getNumberValue = (number) => {
    if (isNaN(number)) return 0;
    return Number(number);
  }

  handleSearch = () => {
    const { BASE_URL } = constantes;
    const {
      filtroSeleccionado,
      filtroBusqueda,
    } = this.state;
    superagent
      .get(`${BASE_URL}/pedidos`)
      .query({ filter: filtroSeleccionado })
      .query({ value: filtroBusqueda })
      .then((pedidos) => {
        this.setState({
          pedidos: pedidos.body.pedidos,
          totalPedidos: pedidos.body.count,
          filtrosActivados: true,
          filtroSeleccionadoFinal: filtroSeleccionado,
          filtroBusquedaFinal: filtroBusqueda,
          page: 0,
        });
      })
      .catch((err) => {
        console.error(err);
      });
  }

  render() {
    const { rowsPerPage, page, pedidos, totalPedidos, filtroSeleccionado, selectedPedido, selectedPedidoServicios, totalManoObra, totalPrecio } = this.state;
    const emptyRows = rowsPerPage - Math.min(rowsPerPage, pedidos.length - page * rowsPerPage);
    return (
      <section className="main-container">
        <h1>CANSA MULTISERVICIOS</h1>
        <div className="space-between">
          Filtros
          <Select
            value={filtroSeleccionado}
            onChange={(event) => {
              const filtro = event.target.value;
              if (filtro === 'fecha') {
                this.setState({ filtroSeleccionado: filtro, filtroBusqueda: moment().format('YYYY-MM-DD') });
              } else {
                this.setState({ filtroSeleccionado: filtro, filtroBusqueda: '' });
              }
            }}
            inputProps={{
              name: 'campo',
              id: 'campo-simple',
            }}>
            <MenuItem value="cliente">Cliente</MenuItem>
            <MenuItem value="placa">Placas</MenuItem>
            <MenuItem value="vehiculo">Vehículo</MenuItem>
            <MenuItem value="folio">Folio</MenuItem>
            <MenuItem value="factura">Factura</MenuItem>
            <MenuItem value="fecha">Fecha</MenuItem>
          </Select>
          {
            filtroSeleccionado !== 'fecha' ?
              <TextField
                id="busqueda"
                value={this.state.filtroBusqueda}
                onChange={(event) => this.setState({ filtroBusqueda: event.target.value })}
                margin="normal"
              />
              :
                <TextField
                  id="date"
                  type="date"
                  onChange={(event) => this.setState({ filtroBusqueda: moment(event.target.value).format('YYYY-MM-DD') })}
                  InputLabelProps={{
                    shrink: true,
                  }}
                />

          }
          <Button variant="contained" color="primary" onClick={this.handleSearch}>
            Buscar
          </Button>
          <Button
            variant="contained"
            color="primary"
            onClick={this.getInitialPedidos}
          >
            Eliminar filtros
          </Button>
        </div>
        <div className="new-order">
          <a href="/nuevo">Nueva orden</a>
        </div>
        <Paper>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>Fecha</TableCell>
                <TableCell>Cliente</TableCell>
                <TableCell>Placas</TableCell>
                <TableCell>Folio</TableCell>
                <TableCell>Factura</TableCell>
                <TableCell>Vehículo</TableCell>
                <TableCell>Acciones</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {pedidos.map(row => (
                <TableRow key={row.id}>
                  <TableCell component="th" scope="row">
                    {row.fecha}
                  </TableCell>
                  <TableCell>
                    {row.cliente && `${row.cliente.nombre} ${row.cliente.telefono}`}
                  </TableCell>
                  <TableCell>{row.placa}</TableCell>
                  <TableCell>{row.folio}</TableCell>
                  <TableCell>{row.factura}</TableCell>
                  <TableCell>
                    {row.vehiculo && `${row.vehiculo.nombre}`}
                  </TableCell>
                  <TableCell>
                    <span
                      className="pdf"
                      onClick={() => {
                        this.getServiciosByPedido(row);
                      }}
                    >
                      PDF
                    </span>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
            <TableFooter>
              <TableRow>
                <TablePagination
                  rowsPerPageOptions={[25]}
                  colSpan={3}
                  count={totalPedidos}
                  rowsPerPage={rowsPerPage}
                  page={page}
                  SelectProps={{
                    native: true,
                  }}
                  onChangePage={this.handleChangePage}
                  onChangeRowsPerPage={this.handleChangeRowsPerPage}
                  ActionsComponent={TablePaginationActionsWrapped}
                />
              </TableRow>
            </TableFooter>
          </Table>
        </Paper>
        {
          selectedPedido &&
          <div className="new-order" style={{ maxWidth: '900px', margin: '24px auto 0' }}>
            <Button
              id="print"
              variant="contained"
              color="primary"
              onClick={this.print}
            >
              Descargar
            </Button>
          </div>
        }
        {
          selectedPedido &&
          <Paper id="pdf-to-download">
            <div style={{ textAlign: 'center', paddingTop: '24px' }}>
              <img style={{ width: '400px' }} src="data:image/png;base64," />
            </div>
            <div className="info">
              <div style={{ color: 'red' }}><b>Folio:</b>{selectedPedido.folio}</div>
              <div><b>Fecha:</b>{selectedPedido.fecha}</div>
              <div><b>Factura:</b>{selectedPedido.factura}</div>
              <div><b>Cliente:</b>{(selectedPedido.cliente && selectedPedido.cliente.nombre) || ''}</div>
              <div><b>Teléfono:</b>{(selectedPedido.cliente && selectedPedido.cliente.telefono) || ''}</div>
              <div><b>Placas:</b>{selectedPedido.placa}</div>
              <div><b>Vehículo:</b>{selectedPedido.vehiculo && selectedPedido.vehiculo.nombre}</div>
            </div>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell>Servicio</TableCell>
                  <TableCell>Cantidad</TableCell>
                  <TableCell>Mano obra</TableCell>
                  <TableCell>No. parte</TableCell>
                  <TableCell>Precio c/u</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {selectedPedidoServicios.map(row => (
                  <TableRow key={row.id}>
                    <TableCell component="th" scope="row">
                      {nombresServicios[row.idServicio - 1]}
                    </TableCell>
                    <TableCell>
                      {row.cantidad}
                    </TableCell>
                    <TableCell>{row.manoObra}</TableCell>
                    <TableCell>{row.numeroParte}</TableCell>
                    <TableCell>{row.precio}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
            <div className="info">
              {
                selectedPedido && selectedPedido.afinacion &&
                <div><b>Incluye afinación</b></div>
              }
              {
                selectedPedido && selectedPedido.cambioAceite &&
                <div><b>Incluye cambio de aceite</b></div>
              }
              <div><b>Otros</b>{numeral(totalManoObra).format('$0,0.00')}</div>
              <div><b>Piezas:</b>{numeral(totalPrecio).format('$0,0.00')}</div>
              <div><b>Otros:</b>{selectedPedido.otros || ''}</div>
              <div><b>Costo otros:</b>{numeral(selectedPedido.costoOtros).format('$0,0.00')}</div>
              <div><b>Total:</b>{numeral(totalManoObra + totalPrecio + selectedPedido.costoOtros).format('$0,0.00')}</div>
            </div>
          </Paper>
        }
      </section>
    );
  }
}

ReactDOM.render(<Home />, document.getElementById('content'));



